export class ConfDevice {
  created_at: string
  credential: any
  id: number
  address: any
  name: string
  port: number
  prompt: string
  protocol: string
  ref: number
  tac_device: number
  tgui_device: string
  updated_at: string

    empty(): void {
      this.id=undefined;
      this.name='';
      this.address = [];
      this.credential = [];
      this.port = 22;
      this.protocol = 'ssh';
    }
}
